    margin-bottom: 10px;
}

.dt__table-head th {
    font-size: 1rem !important;
    font-weight: bold;
}

.dt__wrapper {
    overflow-x: auto;
}

.dt__root {
    width: '100%';
}

.dt__table {
    min-height: 400px;
}