.toolbar {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;

    width: 100%;
    height: 54px;
    background: #272727;
    
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
    text-transform: uppercase;
}

.toolbar__nav {
    height: 100%;
    padding: 0 1rem;

    display: flex;
    align-items: center;
}

.toggle-button {
    margin-right: 1rem;
    background: transparent;
    border: none;
    font-size: 1.5rem;
    color: white;
}

.toggle-button:hover {
    cursor: pointer;
}

.toggle-button:focus {
    outline: none;
}

.toolbar__logo img{
    width: 44px;
    margin-top: 5px;
}

.spacer {
    flex: 1 1;
}

.toolbar__nav-items ul {
    list-style: none;
    margin: 0;
    padding: 0;

    display: flex;
}

.toolbar__nav-items li {
    padding: 0 0.5rem;
}

.toolbar__nav-items a {
    color: white;
    text-decoration: none;
}

.toolbar__nav-items a:hover,
.toolbar__nav-items a:active {
    color: #ff652f;
    -webkit-transition: 200ms;
    transition: 200ms;
}

@media (max-width: 768px) {
    .toolbar__nav-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar__toggle-button {
        display: none;
    }
}
.side-toolbar {
    height: 100%;
    background: #272727;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.25rem;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    text-transform: uppercase;
}

.side-toolbar.open {
    -webkit-transform: translateX(0);
            transform: translateX(0)
}

.side-toolbar ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0;
}

.side-toolbar li {
    margin: 0.5rem 0;
}

.side-toolbar a {
    color: white;
    text-decoration: none;
}

.side-toolbar a:hover,
.side-toolbar a:active {
    color: #ff652f;
}

@media (min-width: 769px) {
    .side-toolbar {
        display: none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 100;
}
.project {
    display: flex;
    flex-direction: column;

    box-sizing: border-box;
    margin: 10px 0px;

    background: #2a2a2a;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    color: white;
}

.project-text-primary {
    color: white;
    font-size: 1rem;
    font-weight: bold;
}

.project-text-secondary {
    font-weight: bold;
    margin-right: 5px;
}

.project__title {
    padding: 10px;
    background: #494949;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
}

.project__info {
    display: flex;
    padding: 10px;
    color: #cccccc;
}

.project__info-description {
    flex: 1 1;
}

.project__info-info {
    flex: 1 1;
}

.project__info-members {
    display: flex;
    flex-direction: row;
}

.project__links {
    display: flex;
    flex-direction: row;
    background: #494949;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
}

.project__links a {
    color: white;
    text-decoration: none;
}

.project__links-repo-btn, 
.project__links-page-btn,
.project__links-info-btn {
    font-size: 0.75rem;
    font-weight: bold;
    height: 100%;
    text-transform: uppercase;
    color: white;
    background: #494949;
    border: none;
    padding: 10px;
}

.fa-github, .fa-file-text-o, .fa-external-link {
    color: white;
    margin: 0 5px 0 0;
}

.project__links-repo-btn:hover, 
.project__links-page-btn:hover,
.project__links-info-btn:hover {
    cursor: pointer;
    background: #7c7c7c;
    -webkit-transition: 200ms;
    transition: 200ms;
}

@media (max-width: 768px) {
    .project__info {
        flex-direction: column;
    }

    .project__info-info {
        margin-top: 20px;
    }
}

@media (min-width: 769px) {
    .project__info {
        flex-direction: row;
    }
}
.info {
    flex: 3 1;

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    color: white;

}

.info__employment, .info__education, .info__skills {
    box-sizing: border-box;
    /* padding: 10px; */
    margin: 10px 0px;
    
    flex-basis: 32%;

    background: #2a2a2a;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.info-title {
    padding: 10px;
    background: #494949;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
}

.info__education {
    display: flex;
    flex-direction: column;
}

.education-background {
    padding: 24px 16px;
    font-size: 1rem;
}

.education-background-primary {
    font-size: 1rem;
}

.education-background-secondary {
    color: #747474;
    font-size: 0.85rem;
}

.divider {
    flex: 1 1;
}

@media (min-width: 769px) {
    .info {    
        display: flex;
        justify-content: space-between;
    }
}

